import './googlemaps.scss';
import * as template from "./googlemaps.hbs";
import * as d3 from 'd3';

export default class GoogleMaps {

  constructor($, _, eventbus, selector, key) {
    this.$ = $;
    this._ = _;
    this.eventbus = eventbus;
    this.script = `https://maps.googleapis.com/maps/api/js?key=${key}&libraries=places`;
    this.mapElement = document.getElementById(selector);
    this.mapElement.innerHTML = template.default();
    this.markers = [];
    this.circles = [];
    this.generateIconCache = {};
  }

  /**
   *
   * @param lat
   * @param lng
   */
  initMap (position, options) {
    if (!position || position === null) {
      position = {lat: 0, lng: 0};
    }
    let {
      zoom = 5,
      zoomControl,
      mapTypeControl,
      scaleControl,
      streetViewControl,
      rotateControl,
      fullscreenControl
    } = options;

    return this.loadScript(this.script).then(() => {
      // The map, centered at position
      this.map = new google.maps.Map(
        this.$(this.mapElement).find("#map")[0],
        {
          zoom,
          disableDefaultUI: true,
          mapTypeControl,
          scaleControl,
          streetViewControl,
          rotateControl,
          fullscreenControl,
          zoomControl,
          center: new google.maps.LatLng(position.lat, position.lng),
        });

      this.infowindow = new google.maps.InfoWindow();


      // Events ......
      google.maps.event.addListener(this.map, 'tilesloaded', (evt) =>
        this.ready = true
      );

      google.maps.event.addListener(this.map, 'zoom_changed', this._.debounce(() => {
        if (this.internalChange) {
          this.internalChange = false;
          return;
        }
        if (this.eventbus && this.ready) {
          this.eventbus.dispatch("bounds_changed", this.getNorthEeastSouthWeast());
        }
      }, 250));


      google.maps.event.addListener(this.map, 'dragend', this._.debounce(() => {
        if (this.internalChange) {
          this.internalChange = false;
          return;
        }
        if (this.eventbus && this.ready) {
          this.eventbus.dispatch("bounds_changed", this.getNorthEeastSouthWeast());
        }
      }, 250));

      let $searchButton = this.$('[data-action]');
      $searchButton.click((e) => {
        this.eventbus.dispatch(this.$(e.currentTarget).data('action'), this.getNorthEeastSouthWeast());
      })
    });
  }

  /**
   *
   * @returns {{ne: {lat: *, lng: *}, sw: {lat: *, lng: *}}}
   */
  getNorthEeastSouthWeast() {
    let bounds = this.map.getBounds();
    let sw = bounds.getSouthWest();
    let ne = bounds.getNorthEast();
    let swNe = {ne: new google.maps.LatLng(ne.lat(), ne.lng()), sw: new google.maps.LatLng(sw.lat(), sw.lng())};
    return swNe;
  }

  /**
   *
   * @param geoList
   * @param helpIndex
   */
  drawCircles(geoList, helpIndex) {
    this.setMarkers(geoList, {visible:false});
    this.internalChange = true;
    let map = this.map;
    let bounds = map.getBounds();

    let center = map.getCenter();
    let ne = bounds.getNorthEast();

    // r = radius of the earth in statute meters
    let r = 6371 * 1000;

    // Convert lat or lng from decimal degrees into radians (divide by 57.2958)
    let lat1 = center.lat();
    let lon1 = center.lng();
    let lat2 = ne.lat();
    let lon2 = ne.lng();

    // distance = circle radius from center to Northeast corner of bounds
    let dis = r * Math.acos(Math.sin(lat1) * Math.sin(lat2) +
      Math.cos(lat1) * Math.cos(lat2) * Math.cos(lon2 - lon1));

    // Construct the circle for each value in citymap.
    // Note: We scale the area of the circle based on the population.
    let totalCount = 0;
    for (let circle of geoList) {
      totalCount = totalCount + parseInt(circle.count);
    }
    for (let circle of geoList) {
      // Add the circle for this city to the map.
      let rad = (parseInt(circle.count) * (dis/4))/totalCount;
      console.log('-------------');
      console.log('circle count: ', parseInt(circle.count));
      console.log('total count: ', totalCount);
      console.log('rad : ', rad);
      console.log('-------------');
      let cityCircle = new google.maps.Circle({
        strokeColor: '#FF0000',
        strokeOpacity: 0.8,
        strokeWeight: 2,
        fillColor: '#FF0000',
        fillOpacity: 0.35,
        map: this.map,
        center: {lat:circle.lat, lng:circle.lng},
        radius:  100000
      });
      cityCircle.addListener('click', () => {

        let infowindow = new google.maps.InfoWindow({
          content: "results: "+circle.count+""
        });
        infowindow.setPosition(cityCircle.getCenter());
        infowindow.open(map);
      });
      this.circles.push(cityCircle);
    }
    this.setMarkers(geoList, {visible:false});
  }

  /**
   *
   */
  initAutocomplete() {
    // Create the search box and link it to the UI element.
    const input = document.getElementById('ifs-input');
    let searchBox = new google.maps.places.SearchBox(input);

    //this.map.controls[google.maps.ControlPosition.TOP_LEFT].push(input);

    // Bias the SearchBox results towards current map's viewport.
    this.map.addListener('bounds_changed', () => {
      searchBox.setBounds(this.map.getBounds());
    });

    let markers = [];
    // Listen for the event fired when the user selects a prediction and retrieve
    // more details for that place.
    searchBox.addListener('places_changed', () => {
      this.deleteMarkers();
      this.deleteCircles();
      let places = searchBox.getPlaces();

      if (places.length === 0) {
        return;
      }

      // Clear out the old markers.
      markers.forEach(function(marker) {
        marker.setMap(null);
      });
      markers = [];

      // For each place, get the icon, name and location.
      let bounds = new google.maps.LatLngBounds();
      places.forEach((place) => {
        if (!place.geometry) {
          console.log("Returned place contains no geometry");
          return;
        }

        if (place.geometry.viewport) {
          // Only geocodes have viewport.
          bounds.union(place.geometry.viewport);
        } else {
          bounds.extend(place.geometry.location);
        }
      });
      this.map.fitBounds(bounds);
    });

  }

  /**
   *
   * @returns {*}
   */
  getZoom() {
    return this.map.getZoom();
  }

  /**
   *
   * @returns {HTMLScriptElement}
   */
  initClusters() {
    let clusterURL = "https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/markerclusterer.js";
    return this.loadScript(clusterURL);
  }

  /**
   *
   * @returns {HTMLElement | null | *}
   */
  getMapElement () {
    return this.mapElement;
  };

  /**
   *
   * @param lat
   * @param lng
   */
  setMarker (lat, lng, label, options) {
    const {visible} = options || true;
    const {icon} = options || null;
    this.internalChange = true;
    let position = new google.maps.LatLng (lat,lng);
    let marker = new google.maps.Marker({
      position,
      map: this.map,
      label: "",
      visible: visible,
      icon: icon
    });
    this.markers.push(marker);

    // marker events ...
    marker.addListener('click', () => {
      this.infowindow.setContent(label);
      this.infowindow.open(this.map, marker);
    });
    marker.addListener('mouseover', () => {
      setTimeout(() => {
        this.infowindow.setContent(label);
        this.infowindow.open(this.map, marker);
      }, 250)

    });
    marker.addListener('mouseout', () => {
      this.infowindow.close();
    });
  }

  /**
   *
   * @param markers
   */
  setMarkersWithLabel (markers) {
    this.internalChange = true;
    let map = this.map;
    this.generateIconCache = {};

    for (let point of markers) {
      this.generateIcon(point.count, (src) => {
        this.setMarker(point.lat, point.lng, point.label, {icon: src});
      });
    }
  }

  /**
   *
   * @param maxZoom
   */
  fixZoom (maxZoom=15) {
    // set max zoom.
    if (this.map.getZoom() > maxZoom){
      this.map.setZoom(maxZoom);
    }

    // set min zoom.
    let minZoom = Math.round(Math.floor(this.$(this.mapElement).width() / 256) / 2);
    if (this.map.getZoom() < minZoom ){
      this.map.setZoom(minZoom)
    }
  }

  /**
   * TODO: refactor this function...
   * @param number
   * @param callback
   */
  generateIcon(number, callback) {
    let generateIconCache = this.generateIconCache;
    if (generateIconCache[number] !== undefined) {
      callback(generateIconCache[number]);
    }
    let minNumberColor= "#f4e541",
      maxNumberColor="#f44242",
      defaultNumberColor = "#f4a341";

    let fontSize = 16,
      imageWidth  = 35, imageHeight = 35, color = minNumberColor;

    if (number >= 1000) {
      fontSize = 10;
      imageWidth  = 55;
      imageHeight = 55;
      color = maxNumberColor;
    } else if (number < 1000 && number > 100) {
      fontSize = 14;
      imageWidth  = 45;
      imageHeight = 45;
      color = defaultNumberColor;
    }

    let svg = d3.select(document.createElement('div')).append('svg')
      .attr('viewBox', '0 0 54.4 54.4')
      .append('g');

    let circles = svg.append('circle')
      .attr('cx', '27.2')
      .attr('cy', '27.2')
      .attr('r', '21.2')
      .style('fill', color)
      .style('opacity', 0.6);

    let path = svg.append('path')
      .attr('d', 'M27.2,0C12.2,0,0,12.2,0,27.2s12.2,27.2,27.2,27.2s27.2-12.2,27.2-27.2S42.2,0,27.2,0z M6,27.2 C6,15.5,15.5,6,27.2,6s21.2,9.5,21.2,21.2c0,11.7-9.5,21.2-21.2,21.2S6,38.9,6,27.2z')
      .attr('fill', color);

    let text = svg.append('text')
      .attr('dx', 27)
      .attr('dy', 32)
      .attr('text-anchor', 'middle')
      .attr('style', 'font-size:' + fontSize + 'px; fill: #FFFFFF; font-family: Arial, Verdana; font-weight: bold');
      //.text(number);

    let svgNode = svg.node().parentNode.cloneNode(true),
      image = new Image();

    d3.select(svgNode).select('clippath').remove();

    let xmlSource = (new XMLSerializer()).serializeToString(svgNode);

    image.onload = (function(imageWidth, imageHeight) {
      let canvas = document.createElement('canvas'),
        context = canvas.getContext('2d'),
        dataURL;

      d3.select(canvas)
        .attr('width', imageWidth)
        .attr('height', imageHeight);

      context.drawImage(image, 0, 0, imageWidth, imageHeight);

      dataURL = canvas.toDataURL();
      generateIconCache[number] = dataURL;

      callback(dataURL);
    }).bind(this, imageWidth, imageHeight);

    image.src = 'data:image/svg+xml;base64,' + btoa(encodeURIComponent(xmlSource).replace(/%([0-9A-F]{2})/g, function(match, p1) {
      return String.fromCharCode('0x' + p1);
    }));
  }

  /**
   *
   * @param markers
   */
  setMarkers (markers, options) {
    for (let marker of markers) {
      this.setMarker(marker.lat, marker.lng, marker.label, options);
    }
    //this.fixZoom();
  }

  fitBounds (boundsList) {
    let bounds = new google.maps.LatLngBounds();
    for (let bound of boundsList) {
      bounds.union(new google.maps.LatLngBounds(new google.maps.LatLng(bound.sw.lat, bound.sw.lng ), new google.maps.LatLng(bound.ne.lat, bound.ne.lng)));
    }
    this.map.fitBounds(bounds);
    this.map.setCenter(bounds.getCenter());
  }

  /**
   *
   */
  deleteMarkers() {
    for (let marker of this.markers) {
      marker.setMap(null);
    }
    this.markers = [];
  }

  /**
   *
   */
  deleteCircles() {
    for (let marker of this.circles) {
      marker.setMap(null);
    }
    this.circles = [];
  }


  /**
   *
   * @param locations
   */
  createClusters (locations) {
    this.internalChange = true;
    // Add some markers to the map.
    // Note: The code uses the JavaScript Array.prototype.map() method to
    // create an array of markers based on a given "locations" array.
    // The map() method here has nothing to do with the Google Maps API.
    let markers = locations.map(function(location, i) {
      return new google.maps.Marker({
        position: location,
        label: location.label
      });
    });

    // Add a marker clusterer to manage the markers.
    let markerCluster = new MarkerClusterer(this.map, markers,
      {imagePath: 'https://developers.google.com/maps/documentation/javascript/examples/markerclusterer/m'});
  }

  /**
   *
   * @param script
   * @returns {HTMLScriptElement}
   */
  loadScript (script) {
    return new Promise((resolve, reject) => {
      // append google maps api script
      let s = document.createElement("script");
      s.type = "text/javascript";
      s.src = script;
      // Use any selector
      document.getElementsByTagName('head')[0].append(s);

      // Important success and error for the promise
      s.onload = function () {
        resolve(s.src);
      };
      s.onerror = function (err) {
        reject(err);
      };
    });
  }


}
